:root{
  --clr-primary:#e1ecfc;
  --clr-secondary: #39f;
  --clr-accent1: #06e;
  --clr-accent2: #5af;
  --clr-accent3: #0061e3;
  --clr-accent4: #3af;
  --clr-light: #fff;
  --clr-body : #fff;
  --clr-danger: #cf1b24;
  --clr-main: none;
  --border1 :  2px solid #39f9;
  --bs-main: none;
  --bs-article: 2px 2px 10px #0003, 10px 10px 20px  #39f5;
  --bs-downloads: inset 0 0 8px #0003 , inset 0 0 15px #0001;
  --bs-downloads-hover: 0 0 6px #0005, 0 0 12px #0003;
  --bs-games:inset 0 0 8px #0005 , inset 0 0 15px #0003;
}
@media(prefers-color-scheme: dark){ 
  :root{   
  --clr-primary:#232834;
  --clr-secondary: #126;
  --clr-accent1: #025;
  --clr-accent2: #128;
  --clr-accent3: #92b3ff;
  --clr-accent4: #126;
  --clr-light: #fff;
  --clr-body : #161009;
  --clr-main: #111320;
  --clr-danger: #8f0a10;
  --border1 :  none;
  --bs-main: inset 0 0 15px #5693, 0 0 10px #5696;
  --bs-article: none;
  --bs-downloads: inset 0 0 8px #0003 , inset 0 0 15px #0001;
  --bs-downloads-hover: 0 0 6px #0005, 0 0 12px #0003;
  --bs-games:inset 0 0 8px #0005 , inset 0 0 15px #0003;
  }
}
*, *:after, *:before{
  margin: 0;
  padding: 0;
  box-sizing: content-box;
}
body{
  background-color: var(--clr-main);
}
.App {
}
.main{
  margin: 0 auto;
  /* background-color: var(--clr-primary);
  box-shadow: var(--bs-article); */
  max-width: 800px;
}

a{
  text-decoration: none;
}
h1{
  font-size: 3rem;
}
h2{
  font-size: 2.5rem;
}
h3{
  font-size: 2rem;
}
h4{
  font-size: 1.25rem;
}
h5{
  font-size: 1rem;
}
.row{
  display: flex;
  flex-direction: row;
}
.column{
  display: flex;
  flex-direction: column;
}

