header{
    justify-content: space-between;
    padding: 1em calc( (100% - 800px) / 2);
    background-color: var(--clr-secondary);
    align-items: center;
}
header h2{
    color: var(--clr-light);
}
nav ul{
    list-style: none;
}
nav ul li{
    margin-left: 1em;
}
nav ul li a{
    color: var(--clr-light);
}